@import '../../../constants.scss';

.signInForm {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 30px;
    max-width: 400px;
    background-color: white;
    border-radius: 20px;
}